
  ---
title: 
---
<template>
  <my-wrapper title="">
    <my-container class="roles">
      <my-title :level="3"></my-title>
      <el-row :gutter="14">
        <el-col :span="8">
          <my-description top align="center">
            <template v-slot:title>
              <el-button type="danger" @click="refreshRole('admin')">Admin</el-button>
            </template>
            <my-paragraph></my-paragraph>
          </my-description>
        </el-col>
        <el-col :span="8">
          <my-description top align="center">
            <template v-slot:title>
              <el-button type="warning" @click="refreshRole('user')">User</el-button>
            </template>
            <my-paragraph></my-paragraph>
          </my-description>
        </el-col>
        <el-col :span="8">
          <my-description top align="center">
            <template v-slot:title>
              <el-button type="success" @click="refreshRole('guest')">Guest</el-button>
            </template>
            <my-paragraph></my-paragraph>
          </my-description>
        </el-col>
      </el-row>
    </my-container>
    <my-panel size="large" title="" shadow="never" :border="false" theme="border-left">
      <template v-slot:handle>
        <el-button type="primary" size="small" :disabled="!$access.has('add')"></el-button>
        <el-button size="small" @click="reload"></el-button>
      </template>
      <my-table ref="table" size="small" :columns="columns" :page-size="5" :loader="loader">
        <template v-slot:state="{row}">
          <el-tag v-if="row.state<3" size="small" type="warning"></el-tag>
          <el-tag v-else size="small" type="success"></el-tag>
        </template>
        <template v-slot:actions="{row}">
          <el-button size="small" :disabled="!$access.has('view')"></el-button>
          <el-button size="small" :disabled="!$access.has('update')"></el-button>
          <el-button size="small" type="warning" :disabled="!$access.has('remove')"></el-button>
          <el-button size="small" type="primary" :disabled="!($access.is('admin') && row.state < 3)"></el-button>
        </template>
      </my-table>
    </my-panel>
    <my-panel size="large"
              title="CRUD"
              shadow="never"
              :border="false"
              :body-style="{padding:0}"
              theme="border-left">
      <template v-slot:handle>
        <el-button type="primary" size="small" :disabled="!$access.has('add')"></el-button>
      </template>
      <my-crud :adapter="adapter" :columns="crudColumns" :table-options="{size:'small'}">
        <template v-slot:state="{row}">
          <el-tag v-if="row.state<3" size="small" type="warning"></el-tag>
          <el-tag v-else size="small" type="success"></el-tag>
        </template>
      </my-crud>
    </my-panel>
  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Col as __Col__,Row as __Row__,Tag as __Tag__} from 'element-ui';
     import {MyContainer as __MyContainer__,MyCrud as __MyCrud__,MyDescription as __MyDescription__,MyPanel as __MyPanel__,MyParagraph as __MyParagraph__,MyTable as __MyTable__,MyTitle as __MyTitle__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import {MyCrud} from '$ui'
  import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        adapter: MyCrud.adapter.call(this, 'MockForExample'),
        columns: [
          {type: 'index', label: '#'},
          {label: '', prop: 'name'},
          {label: '', prop: 'age'},
          {label: '', prop: 'state'},
          {label: '', prop: 'actions', width: 300}
        ],
        crudColumns: [
          {type: 'index', label: '#'},
          {label: '', prop: 'name'},
          {label: '', prop: 'age'},
          {label: '', prop: 'state'},
          {
            label: '',
            type: 'handle',
            width: 350,
            theme: 'button',
            handles: [
              {view: true, access: 'view'},
              {edit: true, access: 'update'},
              {remove: true, access: 'remove'},
              {
                text: '',
                type: 'primary',
                icon: 'el-icon-s-check',
                access: row => {
                  return this.$access.is('admin') && row.state < 3
                }
              }
            ]
          }
        ]
      }
    },
    methods: {
      loader(page, limit) {
        return this.fetchMockForExample({
          data: {page, limit}
        })
      },
      reload() {
        this.$refs.table.refresh()
      },
      refreshRole(account) {
        this.login({
          data: {
            account,
            password: '123456'
          }
        }).then(res => {
          this.$access.login(res)
          this.$message.success(`${account}`)
        })
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Col__);
__vue__.use(__Row__);
__vue__.use(__Tag__)
     __vue__.use(__MyContainer__);
__vue__.use(__MyCrud__);
__vue__.use(__MyDescription__);
__vue__.use(__MyPanel__);
__vue__.use(__MyParagraph__);
__vue__.use(__MyTable__);
__vue__.use(__MyTitle__);
__vue__.use(__MyWrapper__)
    
  </script>

<style lang="scss" scoped>
  .my-container {
    margin-bottom: 14px;
  }
</style>

  